import React,{ useState} from 'react'
import NumberPad from '../components/numberPad'
import Tabs from '../components/tabs'
import Input from '../components/note'
import 'antd/dist/antd.css'
import styled from 'styled-components'
// import Icon from '../components/icon'

const CountMoneyWrap=styled.div`
  height:100%;
  display:flex;
  justify-content: center;
  flex-direction:column;
  .switchAddOrMinus{
    flex:1
  }
`
export default function CountMoney(){//åˆå§‹æ•°æ®ç»“æ„
  let [bookList,setBookList]=useState({
    id:"",
    moneyType: "-",
    amount:"",
    note:"",
    tag:{name:"food",value:"é¤é¥®"},
    createAt:"",
  })

  function handleTabs(type,defaultTag){//ä¿å­˜moneyType
    setBookList(
      Object.assign(bookList,{moneyType:type,tag:defaultTag})//è§£å†³useStateä¸ä¼šè‡ªåŠ¨åˆå¹¶æ›´æ–°å¯¹è±¡çš„ä¸ºé¢˜ï¼ï¼ï¼ï¼ï¼
    )
  }
  function handleTag(tag){//ä¿å­˜tag
    setBookList({...bookList,tag:tag})
  }
  function handleInput(e){//å¤„ç†note
    //æ­¤å¤„å¯èƒ½éœ€è¦å‡½æ•°èŠ‚æµ
    let inputMsg=e.target.value
    setBookList({...bookList,note:inputMsg})
  }
  function handleDefault(){//åˆå§‹åŒ–æ•°æ®
    console.log(bookList,'.....');
    setBookList({...bookList,note:""})
    // setBookList(bookList={
    //   id:"",
    //   moneyType: "-",
    //   amount:"",
    //   note:"",
    //   tag:{name:"others",value:"å…¶ä»–"},
    //   createAt:"",
    // })
    console.log(bookList,'lllll');
  }
  return <CountMoneyWrap>
    <div className="switchAddOrMinus">
      <Tabs tagName={handleTag} moneyType={handleTabs}/>
    </div>
    <div className="note">
      <Input  handleInput={handleInput} value={bookList.note}/>
    </div>
    <NumberPad className="numberPad" value={bookList} handleDefault={handleDefault}/>
  </CountMoneyWrap>
}