export default class Datahelper {
  constructor(dataKey) {
    this.dataKey = dataKey
    // this.primaryKey = primaryKey
  }

  readData() {
    //æ ¹æ®é”®å€¼è¯»å–æœ¬åœ°æ•°æ®
    let strData = localStorage.getItem(this.dataKey)
    //æŠŠjsonæ•°æ®è½¬ä¸ºjså¯¹è±¡ï¼Œå¹¶ä¸”èµ‹å€¼ç»™arrData
    let arrData = []
    if (strData != null) arrData = JSON.parse(strData)
    return arrData
  }

  saveData(arrData) {
    //æ¥æ”¶ä¸€ä¸ªæ•°ç»„å¹¶ä¸“ä¸ºjson
    let strData = JSON.stringify(arrData)
    //å­˜å‚¨åˆ°æœ¬åœ°
    localStorage.setItem(this.dataKey, strData)
  }

  addData(receivedObj) {
    //è¯»å–æœ¬åœ°æ•°æ®è½¬ä¸ºæ•°ç»„
    let arrData = this.readData()
    //æ¥æ”¶æ•°æ®å†…å®¹ï¼Œ
    // let obj={content:conStr}
    let obj = receivedObj
    //å°†å†…å®¹å°è£…åˆ°å¯¹è±¡ï¼Œå¹¶ç”Ÿæˆid
    let newId =arrData.length > 0 ? arrData[arrData.length - 1].id + 1 : 1
    obj.id = newId
    obj.createAt=new Date()
    //å°†å¯¹è±¡åŠ å…¥æ•°ç»„
    arrData.push(obj)
    //å°†æ•°ç»„è½¬ä¸ºå­—ç¬¦ä¸²ï¼Œä¿å­˜åˆ°æœ¬åœ°
    this.saveData(arrData)
    return newId
  }

  removeData(id) {
    //è¯»å–æœ¬åœ°æ•°æ®
    let arrDate = this.readData()
    //æ ¹æ®IDå¯»æ‰¾è¦åˆ é™¤çš„å†…å®¹
    let index = arrDate.findIndex((ele) => {
      return ele.id === id
    })
    //è°ƒç”¨spliceæ–¹æ³•åˆ é™¤æ‰¾å‡ºçš„å¯¹è±¡
    if (index > -1) {
      arrDate.splice(index, 1)
      this.saveData(arrDate)
      return true
    }
    //å°†æ•°ç»„è½¬ä¸ºå­—ç¬¦ä¸²ï¼Œä¿å­˜åˆ°æœ¬åœ°
    return false
    //è¿”å›booleanå€¼è¡¨ç¤ºåˆ é™¤ç»“æœ
  }
  dataConversion(getTime){
  let week=['æ˜ŸæœŸæ—¥','æ˜ŸæœŸä¸€','æ˜ŸæœŸäºŒ','æ˜ŸæœŸä¸‰','æ˜ŸæœŸå››','æ˜ŸæœŸäº”','æ˜ŸæœŸå…­']
  let time=new Date(getTime)
  let year =time.getFullYear()
  let month =time.getMonth()+1
  let day =time.getDate()
  let dayNumber=time.getDay()//è·å–æ˜ŸæœŸæ•°å­—
  let weekDay=week[dayNumber]  //è·å–æ˜ŸæœŸ$
  let hours =time.getHours()
  let minute =time.getMinutes()
  return {year,month,day,weekDay,hours,minute}
  }
}
